.button {
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 5px;
  font-weight: bold;
  background-color: #7ec5ff;
  border: 0;
  color: white;
  transition: background-color 0.12s;
  font-size: inherit;
  min-width: 100px;

  &--active,
  &:hover {
    background-color: #1a92c9;
  }
}